.mapContainer {
  position: relative;
  height: 0;
  width: 100%;
  padding: 0;
  padding-bottom: 90vh;
}

.mapContainer svg {
  position: absolute;
  height: 90vh;
  width: 100%;
  left: 0px;
  top: 0;
}

.mapContainer svg:hover {
  cursor: grab;
}

.tooltip {
  background-color: #fff!important;
  opacity: 1!important;
  text-align: left!important;
  font-family: GT-Walsheim-Medium, sans-serif;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.16);
  max-width: 180px;
  padding: 0 16px;
}

.tooltip:after {
  border-top-color: #fff !important;
  border-top-style: solid !important;
  border-top-width: 7px !important;
}

.tooltip h1 {
  font-size: 16px;
  color: #1B1D1F!important;
  margin: 10px 0 0 0;
}

.tooltip p {
  font-size: 12px;
  color: #818D99!important;
  margin: 0 0 10px 0;
  line-height: 14px;
}

.svgPanZoom div {
  display: none!important;
}

.mdc-dialog__surface {
  max-width: 64vw!important;
  min-width: 32vw!important;
  border-radius: 4px!important;
  padding: 36px 48px 42px 48px;
  max-height: 60vh;
  display: flex;
  flex-direction: column;
}

.mdc-dialog__header {
  padding: 1.5rem 2rem!important;
  width: 94%;
  margin: auto;
  margin-bottom: 3rem;
  max-width: 90vw!important;
  background-color: #ffffff;
  position: fixed!important;
  top: 0;
  left: 0;
}

.mdc-dialog__header__title {
  font-size: 24px!important;
  font-family: GT-Walsheim-Medium, sans-serif!important;
  line-height: 34px!important;
}

.mdc-dialog__body::-webkit-scrollbar {
  background: #fff;
  width: 8px;
}

.mdc-dialog__body::-webkit-scrollbar-thumb {
  background: #dfdfdf;
  border-radius: 5px
}

.mdc-dialog__body {
  margin-top: 3rem!important;
  padding: 0!important;
  overflow-y: auto;
}

.dialogField {
  display: flex;
  align-items: center;
  font-family: GT-Walsheim-Medium, sans-serif;
  min-height: 5rem;
  margin-bottom: 1rem;
}

.dialogFieldTitle {
  height: 84px;
}

.dialogFieldSuper {
  font-size: 24px;
  position: relative;
  bottom: 25px;
  text-transform: uppercase;
}

.dialogFieldDesc {
  padding-left: 40px;
  color: #818D99;
  font-size: 16px;
  font-family: GT-Walsheim-Regular, sans-serif;
  line-height: 23px;
}

.dialogFieldTitle, .dialogFieldSuper {
  color: #1B1D1F;
}

.close {
  border: none;
  outline: none;
  background-color: #fff;
}

.close:hover {
  cursor: pointer;
}

.close>.material-icons {
  padding: 10px;
}

.close>.material-icons:hover {
  cursor: pointer;
}

.dialogField h1 {
  margin: 0;
  line-height: 84px;
  width: 50px;
}

.dialogField p {
  margin: 0;
}

.dialogField .dialogFieldTitle {
  font-size: 60px;
}

.cls-1 {
  clip-path: url(#clip-jss-map);
}

.cls-2 {
  fill: #eee;
}

.cls-3 {
  fill: #424242;
}

.cls-4 {
  fill: #8debaf;
}

.cls-5 {
  fill: #dce1e6;
}

.cls-6 {
  fill: #818d99;
}

.cls-7, .cls-9 {
  fill: #b8c2cc;
}

.cls-7:hover {
  cursor: pointer;
}

.cls-8, .cls-9 {
  stroke: none;
}

.cls-10 {
  fill: #f5f5f5;
}

@media only screen and (orientation:portrait) and (max-width: 768px) {
  #tooltip {
    display: none!important;
  }
  svg {
    top: -40px!important;
    left: -700px!important;
  }
  .mdc-dialog__surface {
    min-width: 80vw!important;
    transform: scale(0.8)!important;
  }
  .mdc-dialog__header {
    width: 83%;
  }
  .mdc-dialog__body::-webkit-scrollbar {
    display: none
  }
}